/* -------------------------------
 * *  gird system (grid layout)
 * * ---------------------------- */
.l-grid-layout {
  display: grid;
  padding: 0 calc(8px * 2);
}
.l-grid-layout.__col-quad {
  grid-template: auto/repeat(4, 1fr);
  grid-gap: 16px;
}
.l-grid-layout.__col-triple {
  grid-template: auto/repeat(3, 1fr);
  grid-gap: 16px;
}
.l-grid-layout.__col-double {
  grid-template: auto/repeat(2, 1fr);
  grid-gap: 16px;
}
.l-grid-layout.__col-single {
  grid-template: auto/100%;
}
.l-grid-layout.__col-sidebar-left {
  grid-template: auto/minmax(auto, 300px) 1fr;
  grid-gap: 16px;
}
.l-grid-layout.__col-sidebar-right {
  grid-template: auto/1fr minmax(auto, 300px);
  grid-gap: 16px;
}
@media (max-width: 768px) {
  .l-grid-layout.__col-quad, .l-grid-layout.__col-triple, .l-grid-layout.__col-double, .l-grid-layout.__col-sidebar-left, .l-grid-layout.__col-sidebar-right {
    grid-template: auto/100%;
    grid-gap: 0;
  }
}
.l-grid-layout .l-grid-item {
  margin: 0;
  padding: 0;
}

